import React, {Component} from 'react';

export default class AnnounsmentItem extends Component {
constructor() {
    super();
    this.delAnnounsmentItem = () => {
        console.log(`deleted ${this.props.announcement.title}`)
    }
}

    render(){
        const {announcement} = this.props;
        return (


            <li key={announcement.date.toString()}>
                <h4>{announcement.title}</h4>
                <p>{announcement.description}</p>
                <span>{announcement.date}</span> <br/>
                <button className='edit'
                >Edit</button>
                <button
                    className='del'
                    onClick = {this.delAnnounsmentItem}>
                    Delete
                </button>

            </li>


        );
    }
}
