import React, {Component} from 'react';


export class Add extends Component {

    render() {
        return (
            <div className="Add">
                <h2>Add announcement</h2>
                <form>
                    <label>Title</label><input type='text'/><br/>
                    <label>Description</label><textarea/><br/>

                    <button type='submit'>Add</button>
                </form>
            </div>
        );
    }
}
